<template>
  <PageWrapper
    :detail="true"
    dense
    contentFullHeight
    fixedHeight
    contentClass="fix-detail-page"
    style="position: relative"
  >
    <div class="load">
      <div class="process-box">
        <!-- 标题 -->
        <div class="header-box">
          <BillTitle :options="billTitleOptions" />
          <WfApproveBox
            @save="onSave"
            @submit="onSubmit"
            :processStatus="formState.status"
            :processInstanceId="formState.processInstanceId"
          />
        </div>
        <!-- 表单内容 -->
        <div class="content">
          <div class="left-panel"
            ><a-form ref="weekReportFormRef" :model="formState" name="weekReportForm">
              <a-card title="基本信息" :bordered="false">
                <a-row :gutter="32">
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      label="电站名称"
                      name="stationName"
                      labelAlign="right"
                      :labelCol="{ span: 4 }"
                    >
                      <a-input disabled v-model:value="formState.stationName" style="width: 100%" />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      label="分期"
                      name="periodName"
                      labelAlign="right"
                      :labelCol="{ span: 4 }"
                    >
                      <a-input disabled v-model:value="formState.periodName" />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      label="周报日期"
                      name="reportDay"
                      labelAlign="right"
                      :labelCol="{ span: 4 }"
                      :rules="[{ required: true, message: '请选择日期' }]"
                    >
                      <a-date-picker
                        v-model:value="formState.reportDay"
                        style="min-width: 100%"
                        :disabled="iProcess"
                        @change="changeReportDay"
                        value-format="YYYY-MM-DD"
                      />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      label="周报周期"
                      name="weeklyCycle"
                      labelAlign="right"
                      :labelCol="{ span: 4 }"
                    >
                      <a-input disabled v-model:value="formState.weeklyCycle" />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      label="周发电量(kWh)"
                      name="genElec"
                      labelAlign="right"
                      :labelCol="{ span: 4 }"
                    >
                      <a-input disabled v-model:value="formState.genElec" />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      label="理论发电(kWh)"
                      name="theoElec"
                      labelAlign="right"
                      :labelCol="{ span: 4 }"
                    >
                      <a-input disabled v-model:value="formState.theoElec" />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      label="外接厂用电(kWh)"
                      name="facUsed"
                      labelAlign="right"
                      :labelCol="{ span: 4 }"
                    >
                      <a-input disabled v-model:value="formState.facUsed" />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      label="上网电量(kWh)"
                      name="onlineElec"
                      labelAlign="right"
                      :labelCol="{ span: 4 }"
                    >
                      <a-input disabled v-model:value="formState.onlineElec" />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      label="下网电量(kWh)"
                      name="offlineElec"
                      labelAlign="right"
                      :labelCol="{ span: 4 }"
                    >
                      <a-input disabled v-model:value="formState.offlineElec" />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      label="损失电量(kWh)"
                      name="totalLoss"
                      labelAlign="right"
                      :labelCol="{ span: 4 }"
                    >
                      <a-input disabled v-model:value="formState.totalLoss" />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      label="利用小时数(h)"
                      name="validHours"
                      labelAlign="right"
                      :labelCol="{ span: 4 }"
                    >
                      <a-input disabled v-model:value="formState.validHours" />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      label="综合厂用电率(%)"
                      name="facRatio"
                      labelAlign="right"
                      :labelCol="{ span: 4 }"
                    >
                      <a-input disabled v-model:value="formState.facRatio" />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      label="系统效率(%)"
                      name="sysEfficiency"
                      labelAlign="right"
                      :labelCol="{ span: 4 }"
                    >
                      <a-input disabled v-model:value="formState.sysEfficiency" />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      label="平均温度(℃)"
                      name="avgTemp"
                      labelAlign="right"
                      :labelCol="{ span: 4 }"
                    >
                      <a-input disabled v-model:value="formState.avgTemp" />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      label="平均风速(m/s)"
                      name="avgWind"
                      labelAlign="right"
                      :labelCol="{ span: 4 }"
                    >
                      <a-input disabled v-model:value="formState.avgWind" />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      label="周辐照值(万kj/㎡)"
                      name="radiation"
                      labelAlign="right"
                      :labelCol="{ span: 4 }"
                    >
                      <a-input disabled v-model:value="formState.radiation" />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="24" class="textarea-item">
                    <a-form-item
                      label="补充说明"
                      name="remark"
                      labelAlign="right"
                      :labelCol="{ span: 2 }"
                      :rules="[{ required: true, message: '请输入补充说明' }]"
                    >
                      <a-textarea
                        :rows="10"
                        v-model:value="formState.remark"
                        placeholder="1000字以内"
                        :maxlength="1000"
                        :disabled="iProcess"
                        show-count
                      />
                    </a-form-item>
                  </a-col>
                </a-row>
              </a-card>
            </a-form>

            <a-card title="损失电量" :bordered="false">
              <div style="margin: 0 16px 10px">
                <a-table
                  :columns="lossCloumns"
                  :data-source="lossList"
                  :pagination="false"
                  size="small"
                  bordered
                  :row-selection="rowSelection"
                  @change="handleTableYxChange"
                  :scroll="{ x: 480, y: 620 }"
                >
                  <template #bodyCell="{ column, text, record }">
                    {{ text }}
                  </template>
                </a-table>
              </div>
            </a-card>
          </div>
        </div>
      </div>
    </div>
  </PageWrapper>
</template>
<script lang="ts" setup>
  import { onMounted, reactive, ref, unref, UnwrapRef } from 'vue';
  import BillTitle from '/@/components/Framework/BillTitle/BillTitle.vue';
  import { useI18n } from '@/hooks/web/useI18n';
  import { useMessage } from '@/hooks/web/useMessage';
  import { useRouter } from 'vue-router';
  import { message, FormInstance } from 'ant-design-vue';
  import WfApproveBox from '/@/components/Framework/WorkFlow/WfApproveBox.vue';
  import * as ProcessInstanceApi from '@/api/bpm/processInstance';
  import { MsgManager } from '@/message/MsgManager';
  import { closeCurrentTab } from '@/utils/route';
  import { SysMessage } from '/@/hooks/web/useMessage';
  import { getBaseInfo, getLossCloumns, getLossList, insertWeekReport, getWeekReport } from './index';
  import { assign } from 'lodash-es';
  import { addTabPage } from '@/utils/route';
  import { PageWrapper } from '/@/components/Page';
  import { Memory } from '@/router/index';
  const iProcess = ref<boolean>(false);
  const { t } = useI18n();
  const router = useRouter();
  const { currentRoute } = router;
  const route = unref(currentRoute);
  const query = Memory.getInstance().getQuery(); // route.query;

  const processInstanceId = ref(null);
  const lossCloumns = getLossCloumns('week');

  const lossList = ref([]);
  const weekReportFormRef = ref<FormInstance>();
  const formState: UnwrapRef<FormState> = reactive({ status: 0 });

  const billTitleOptions = reactive<any>({});
  billTitleOptions.subject = '生产周报';
  billTitleOptions.infoItems = [];

  // 加载日报汇总的基础数据
  const loadBaseInfo = async () => {
    if (formState.reportDay == null) {
      return;
    }
    formState.requestType = 'week';
    let data = await getBaseInfo(formState);

    formState.weeklyCycle = data.result.weeklyCycle;
    formState.genElec = data.result.genElec;
    formState.offlineElec = data.result.offlineElec;
    formState.onlineElec = data.result.onlineElec;
    formState.theoElec = data.result.theoElec;
    formState.facUsed = data.result.facUsed;
    formState.totalLoss = data.result.totalLoss;
    formState.validHours = data.result.validHours;
    formState.facRatio = data.result.facRatio;
    formState.sysEfficiency = data.result.sysEfficiency;
    formState.avgTemp = data.result.avgTemp;
    formState.avgWind = data.result.avgWind;
    formState.radiation = data.result.radiation;
    formState.connectCapacity = data.result.connectCapacity;
  };
  // 加载周损失电量数据
  const loadLossList = async () => {
    if (formState.reportDay == null) {
      return;
    }
    formState.requestType = 'week';
    let data = await getLossList(formState);
    lossList.value = data.result;
  };

  // 日期选择后更新并网容量，加载损失电量数据
  async function changeReportDay() {
    // 查询并网容量
    await loadBaseInfo();
    loadLossList();
  }
  //-------------------------------------------------------流程相关操作-------------------------------------------------------------

  const onSave = () => {
    doSave(0);
  };

  const onSubmit = () => {
    doSave(1);
  };

  const doSave = (status: number) => {
    weekReportFormRef.value.validateFields().then(() => {
      let param = formState;
      param.status = status;
      param.lossData = lossList.value;
      insertWeekReport(param)
        .then((data) => {
          //关闭弹窗
          SysMessage.getInstance().success(t('common.saveSuccessText'));
          formState.id = data.result;
          loadBizData();
          if (status >= 1) {
            setTimeout(function () {
              MsgManager.getInstance().sendMsg('day-report', { key: 'week' });
              closeCurrentTab();
            }, 200);
          }
        })
        .catch(() => {});
    });
  };

  const loadBizInfoData = async (res: any) => {
    let { billCode, fillinDate, personMemberName, deptName } = res;
    Object.assign(billTitleOptions, { billCode, fillinDate, personMemberName, deptName });
  };

  const loadBizData = async () => {
    let res = await getWeekReport(formState.id);
    // 填充业务数据
    iProcess.value = res.status > 0;
    // 填充流程数据
    loadBizInfoData(res);
    assign(formState, res);
    if (!formState.processInstanceId) {
      formState.processInstanceId = query.processInstanceId;
    }
    // 加载损失电量数据
    loadLossList();
  };

  // 填充电站分期数据
  const loadPeriodData = async () => {
    // 填充流程数据
    let res = await getWeekReport('');
    loadBizInfoData(res);
    formState.billCode = res.billCode;
    formState.fillinDate = res.fillinDate;
    formState.stationId = query.stationId;
    formState.stationName = query.stationName;
    formState.periodId = query.periodId;
    formState.periodName = query.periodName;
    formState.stationOrganId = query.organId;
    formState.stationOrganName = query.organName;
    loadBaseInfo();
  };
  // 获取分期的并网容量
  function getPeriodInfo() {
    formState.connectCapacity = 1111;
  }
  onMounted(async () => {
    assign(formState, query);
    if (query.id) {
      await loadBizData();
    } else if (query.processInstanceId) {
      processInstanceId.value = query.processInstanceId as unknown as string;
      formState.processInstanceId = query.processInstanceId as unknown as string;
      ProcessInstanceApi.getProcessInstance(formState.processInstanceId).then(async (res) => {
        formState.id = res.businessKey;
        await loadBizData();
      });
    } else {
      // 填充参数数据
      loadPeriodData();
    }
  });
</script>

<style lang="less" scoped>
  .fix-detail-page {
    :deep(&.vben-page-wrapper-content.detail-content) {
      margin: 0;
    }
  }
</style>
